





























import Vue from 'vue'
import { mapState } from 'vuex'
import { map } from 'lodash'

// Components
import TheMap from '@/components/TheMap'
import TheMapSideBar from '@/components/TheMapSideBar'

// Definitions
import IMapMarker from '@/components/definitions/IMapMarker.d'

// Store
import { FETCH_CONFLICTS } from '@/store/modules/conflicts/action-types'
import { SET_SELECTED_CONFLICT_ID } from '@/store/modules/conflicts/mutation-types'


export default Vue.extend({
    name: 'Map',

    components: {
        TheMap,
        TheMapSideBar,
    },

    computed: {
        ...mapState({
            // @ts-ignore
            conflicts: state => state.conflicts.items,
            // @ts-ignore
            isLoading: state => state.isLoading,
            // @ts-ignore
            selectedConflictId: state => state.conflicts.selectedConflictId,
        }),

        markers(): Array<IMapMarker> {
            // @ts-ignore
            return map(this.conflicts, conflict => ({
                id: conflict.id,
                latitude: conflict.latitude,
                longitude: conflict.longitude,
            }))
        },

        selectedConflict(): any | null {
            // TODO https://github.com/vuejs/vue/issues/8721
            // @ts-ignore
            return this.conflicts.find(
                // @ts-ignore
                (conflict: any) => this.selectedConflictId === conflict.id,
            ) || null
        },
    },

    methods: {
        handleMarkerClick(conflictId: number): void {
            this.$store.commit(SET_SELECTED_CONFLICT_ID, conflictId)
        },

        handleDetailsClose(): void {
            this.$store.commit(SET_SELECTED_CONFLICT_ID, null)
        },
    },

    async created() {
        await this.$store.dispatch(FETCH_CONFLICTS)
    },
})
