







































import Vue from 'vue'
import {
    mapActions,
    mapGetters,
} from 'vuex'

// Definitions
import INavElementView from './definitions/INavElementView.d'

// Getters
import {
    IS_AUTHENTICATED,
} from '../store/modules/auth/getter-types'

// Actions
import {
    FIREBASE_AUTH_LOGOUT,
} from '../store/modules/auth/action-types'


interface IComponentData {
    headerData: {
        headerLogoText: string
    }

    headerView: {
        navigation: {
            items: Array<INavElementView>
        },
    },
}

export default Vue.extend({
    name: 'TheHeader',

    data: (): IComponentData => ({
        headerData: {
            headerLogoText: 'ЗАБАСТКОМ',
        },

        headerView: {
            navigation: {
                items: [
                    {
                        title: 'ГЛАВНАЯ',
                        path: '/events',
                    },
                    {
                        title: 'КАРТА',
                        path: '/map',
                    },
                    // {
                    //     title: 'ВОЙТИ',
                    //     path: '/login',
                    // },
                ],
            },
        },
    }),

    computed: {
        ...mapGetters({
            isUserAuthenticated: IS_AUTHENTICATED,
        }),

        visibleMenuItems(): Array<INavElementView> {
            return this.headerView.navigation.items.filter((item) => {
                if (this.isUserAuthenticated) {
                    return item.path !== '/login'
                }

                return true
            })
        },
    },

    methods: {
        ...mapActions({
            logOut: FIREBASE_AUTH_LOGOUT,
        }),
    },
})
