import { Module } from 'vuex'

// Definitions
import IRootState from '@/store/definitions/IRootState.d'
import IReferencesState from './definitions/IReferencesState.d'

import { FETCH_REFERENCES } from './action-types'
import { SET_REFERENCES } from './mutation-types'

import { fetchReferences } from '@/api'



const referencesModule: Module<IReferencesState, IRootState> = {

    state: {
        eventTypes: null,
        eventStatuses: null,
        conflictReasons: null,
        conflictResults: null,
        industries: null,
        regions: null,
    },

    getters: {

    },

    mutations: {

        [SET_REFERENCES]: (
            state: any,
            references: IReferencesState,
        ) => {
            state.eventTypes = references.eventTypes
            state.eventStatuses = references.eventStatuses
            state.conflictReasons = references.conflictReasons
            state.conflictResults = references.conflictResults
            state.industries = references.industries
            state.regions = references.regions
        },

    },

    actions: {

        [FETCH_REFERENCES]: async (
            { commit },
        ): Promise<void> => {

            try {
                const references = await fetchReferences()

                commit(SET_REFERENCES, references)
            }
            catch (error) {
                commit(SET_REFERENCES, null)

                throw error
            }
        },

    },

}

export default referencesModule
