/* eslint-disable import/no-extraneous-dependencies */
import Vue from 'vue'
import { sync } from 'vuex-router-sync'

import './theme/index.css'
import ElementUI from 'element-ui'
// @ts-ignore
import ElementLocaleRu from 'element-ui/lib/locale/lang/ru-RU'

import moment from 'moment'

import L from 'leaflet'
import 'leaflet/dist/leaflet.css'

import store from '@/store'
import { FETCH_REFERENCES } from '@/store/modules/references/action-types'

import router from '@/router'
import App from '@/App'

import { checkUserAuthState } from '@/utils'


(async function () {
    Vue.config.productionTip = false

    // Синхронизация роутера с хранилищем
    sync(store, router)

    // Инициализация ElementUI
    Vue.use(ElementUI, {
        locale: ElementLocaleRu,
    })

    moment.locale('ru')

    // Взято с https://github.com/KoRiGaN/Vue2Leaflet/blob/8253fce54c51b39d95ef7389da675cc0d595ec09/examples/src/main.js
    // без этого карта работает некорректно.
    // @ts-ignore
    delete L.Icon.Default.prototype._getIconUrl

    L.Icon.Default.mergeOptions({
        iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
        iconUrl: require('leaflet/dist/images/marker-icon.png'),
        shadowUrl: require('leaflet/dist/images/marker-shadow.png'),
    })

    const vm = new Vue({
        router,
        store,
        render: h => h(App),
    })

    await checkUserAuthState(store)

    await store.dispatch(FETCH_REFERENCES)

    vm.$mount('#app')
}())
