import firebase from 'firebase/app'
import 'firebase/auth'

import {
    isFinite,
    isInteger,
} from 'lodash'

import {
    SET_AUTH_TOKEN,
} from '@/store/modules/auth/mutation-types'



// Local Storage
export function setAuthTokenExistsToStorage(): void {
    window.localStorage.setItem('isAuthTokenExists', 'true')
}

export function getAuthTokenExistsFromStorage(): boolean {
    return !!window.localStorage.getItem('isAuthTokenExists')
}

export function removeAuthTokenExistsFromStorage(): void {
    window.localStorage.removeItem('isAuthTokenExists')
}


// Firebase
export const firebaseInstanse = firebase.initializeApp({
    apiKey: 'AIzaSyAytWdi8t7fdpwEh_Op62qq-FmGxmaE3fM',
    authDomain: 'strikecom-7ad08.firebaseapp.com',
    databaseURL: 'https://strikecom-7ad08.firebaseio.com',
    projectId: 'strikecom-7ad08',
    storageBucket: 'strikecom-7ad08.appspot.com',
    messagingSenderId: '438907362423',
})

export function checkUserAuthState(store: any): Promise<string|null> {
    return new Promise((resolve) => {
        firebaseInstanse.auth().onAuthStateChanged(async (user): Promise<void> => {
            if (user) {
                const authToken = await user.getIdToken()

                store.commit(SET_AUTH_TOKEN, authToken)
                setAuthTokenExistsToStorage()
                resolve(authToken)
            }
            else {
                store.commit(SET_AUTH_TOKEN, null)
                removeAuthTokenExistsFromStorage()
                resolve(null)
            }
        })
    })
}


export function isNaturalNumber(number: number): boolean {
    return (
        isFinite(number) &&
        isInteger(number) &&
        number > 0
    )
}
