





































import Vue from 'vue'
import moment from 'moment'

import { fetchEvents } from '@/api'

// Route names
import { EVENT_DETAILS_ROUTE_NAME } from '@/router/route-names'


export default Vue.extend({
    name: 'TheMapSideBar',

    props: {
        // Объект конфликта
        conflict: {
            type: Object,
            required: true,
        },
    },

    data: (): any => ({
        relatedEvents: null,
    }),

    async created() {
        this.relatedEvents = await fetchEvents({
            filters: {
                conflict_id: this.$props.conflict.id,
            },
        })
    },

    methods: {
        formatDate(date: number): string {
            return moment(date).format('D MMMM YYYY')
        },

        onCardClick(eventId: number): void {
            this.$router.push({
                name: EVENT_DETAILS_ROUTE_NAME,
                params: {
                    id: eventId,
                },
            })
        },

        onCloseIconClick(): void {
            this.$emit('close-details')
        },
    },
})
