import Vue from 'vue'
import Vuex from 'vuex'

// Definitions
import IRootState from './definitions/IRootState.d'

// Modules
import authModule from './modules/auth'
import referencesModule from './modules/references'
import conflictsModule from './modules/conflicts'
import eventsModule from './modules/events'

// Mutations
import {
    START_LOADING,
    FINISH_LOADING,
} from './mutation-types'


Vue.use(Vuex)

const store = new Vuex.Store<IRootState>({
    modules: {
        auth: authModule,
        references: referencesModule,
        conflicts: conflictsModule,
        events: eventsModule,
    },

    state: {
        isLoading: false,
        isLoaded: false,
    },

    mutations: {

        [START_LOADING](
            state: IRootState,
        ) {
            state.isLoaded = false
            state.isLoading = true
        },

        [FINISH_LOADING](
            state: IRootState,
        ) {
            state.isLoaded = true
            state.isLoading = false
        },
    },

    actions: {

    },
})

export default store
