import { Module } from 'vuex'
import 'firebase/auth'

import {
    firebaseInstanse,
    setAuthTokenExistsToStorage,
    removeAuthTokenExistsFromStorage,
} from '@/utils/index'

// Definitions
import IRootState from '@/store/definitions/IRootState.d'
import IAuthState from './definitions/IAuthState.d'
import ICredentials from './definitions/ICredentials.d'

import {
    FIREBASE_AUTH_LOGIN,
    FIREBASE_AUTH_LOGOUT,
} from './action-types'

import {
    START_AUTH_LOADING,
    FINISH_AUTH_LOADING,
    SET_AUTH_TOKEN,
} from './mutation-types'

import {
    AUTH_TOKEN,
    IS_AUTHENTICATED,
} from './getter-types'



const FIREBASE_USER_NOT_FOUND_ERROR_CODE = 'auth/user-not-found'

const authModule: Module<IAuthState, IRootState> = {

    state: {
        isLoading: false,

        authToken: null,
    },

    getters: {

        [IS_AUTHENTICATED]: (state: IAuthState): boolean => !!state.authToken,

        [AUTH_TOKEN]: (state: IAuthState): string => state.authToken,

    },

    mutations: {

        [START_AUTH_LOADING]: (
            state: IAuthState,
        ) => {
            state.isLoading = true
        },

        [FINISH_AUTH_LOADING]: (
            state: IAuthState,
        ) => {
            state.isLoading = false
        },

        [SET_AUTH_TOKEN]: (
            state: IAuthState,
            authToken: string,
        ) => {
            state.authToken = authToken
        },

    },

    actions: {

        /**
         * Отправить запрос к Firebase для авторизации. Если пользователя с
         * переданными параметрами не найдено, создать его.
         */
        [FIREBASE_AUTH_LOGIN]: async (
            { commit },
            credentials: ICredentials,
        ): Promise<void> => {
            commit(START_AUTH_LOADING)
            commit(SET_AUTH_TOKEN, null)

            try {
                await sendFirebaseAuthAction(
                    credentials,
                    'signIn',
                    commit,
                )
            }
            catch (signInError) {
                if (signInError.code === FIREBASE_USER_NOT_FOUND_ERROR_CODE) {
                    await sendFirebaseAuthAction(
                        credentials,
                        'createUser',
                        commit,
                    )

                    return
                }

                commit(FINISH_AUTH_LOADING)

                throw signInError
            }
        },

        /**
         * Разлогиниться в Firebase
         */
        [FIREBASE_AUTH_LOGOUT]: async ({ commit }): Promise<void> => {
            try {
                await firebaseInstanse.auth().signOut()
                commit(SET_AUTH_TOKEN, null)
                removeAuthTokenExistsFromStorage()
            }
            catch (error) {
                throw error
            }
        },
    },

}

export default authModule


async function sendFirebaseAuthAction(
    credentials: ICredentials,
    action: 'signIn' | 'createUser',
    commit: any,
): Promise<void> {
    let firebaseCredential

    try {
        firebaseCredential = action === 'signIn'
            ? await firebaseInstanse.auth().signInWithEmailAndPassword(
                credentials.email,
                credentials.password,
            )
            : await firebaseInstanse.auth().createUserWithEmailAndPassword(
                credentials.email,
                credentials.password,
            )

        // @ts-ignore
        const authToken = await firebaseCredential.user.getIdToken()

        commit(SET_AUTH_TOKEN, authToken)
        setAuthTokenExistsToStorage()

        commit(FINISH_AUTH_LOADING)
    }

    catch (error) {
        throw error
    }
}
