import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/Home'
import EventDetails from '@/views/EventDetails'
// import Login from '@/views/Login'
import Map from '@/views/Map'

import { getAuthTokenExistsFromStorage } from '@/utils'

import {
    HOME_ROUTE_NAME,
    LOGIN_ROUTE_NAME,
    MAP_ROUTE_NAME,
    EVENT_DETAILS_ROUTE_NAME,
} from './route-names'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            redirect: '/events',
        },

        {
            path: '/events',
            name: HOME_ROUTE_NAME,
            component: Home,
        },

        {
            path: '/events/:id',
            name: EVENT_DETAILS_ROUTE_NAME,
            component: EventDetails,
        },

        // {
        //     path: '/login',
        //     name: LOGIN_ROUTE_NAME,
        //     component: Login,
        // },

        {
            path: '/map',
            name: MAP_ROUTE_NAME,
            component: Map,
        },

        // Фоллбэк для неизвестных роутов
        {
            path: '*',
            redirect: '/',
        },
    ],
})

router.beforeEach((to, from, next) => {
    // Если авторизованный пользователь переходит на состояние авторизации...
    if (
        to.name === LOGIN_ROUTE_NAME &&
        getAuthTokenExistsFromStorage()
    ) {
        // ... перенаправить его в корневое состояние
        next('/')
    }

    // Иначе переход на целевое состояние
    else {
        next()
    }
})

export default router
