import axios, { AxiosResponse, AxiosError } from 'axios'
import Qs from 'qs'

import { BASE_API_URL } from '@/configs/global'


export const axiosInstance = axios.create({
    baseURL: BASE_API_URL,

    paramsSerializer: params => Qs.stringify(params, {
        encode: false,
    }),

    headers: {
        // 'Authorization': `Bearer ${store.getters[AUTH_TOKEN]}`,
    },
})


export async function fetchReferences(): Promise<any> {
    return axiosInstance.request({
        method: 'GET',
        url: 'references',
    })
        .then((response): AxiosResponse<any> => response.data)
}


export async function fetchConflicts(params ?: any): Promise<any> {
    return axiosInstance.request({
        method: 'GET',
        url: 'conflict',
        params,
    })
        .then((response): AxiosResponse<any> => response.data)
}


export async function fetchEvents(params = {}): Promise<any> {
    return axiosInstance.request({
        method: 'GET',
        url: 'event',
        params,
    })
        .then((response): AxiosResponse<any> => response.data)
}

export async function fetchEvent(params: any): Promise<any> {
    return axiosInstance.request({
        method: 'GET',
        url: `event/${params.id}`,
    })
        .then((response): AxiosResponse<any> => response.data)
}

export async function createEvent(data: any): Promise<any> {
    return axiosInstance.request({
        method: 'POST',
        url: 'event',
        data: {
            ...data,
        },
    })
        .then((response): AxiosResponse<any> => response)
        .catch((error): AxiosError => {
            throw error
        })
}
