import { Module } from 'vuex'

// Definitions
import IRootState from '@/store/definitions/IRootState.d'
import IConflictsState from './definitions/IConflictsState.d'

import { FETCH_CONFLICTS } from './action-types'
import {
    FINISH_FETCHING_CONFLICTS,
    SET_SELECTED_CONFLICT_ID,
} from './mutation-types'
import {
    START_LOADING,
    FINISH_LOADING,
} from '@/store/mutation-types'
import { GET_CONFLICT } from './getter-types'

import { fetchConflicts } from '@/api'



const conflictsModule: Module<IConflictsState, IRootState> = {

    state: {
        items: [],
        selectedConflictId: null,
    },

    getters: {

        [GET_CONFLICT]: (state: IConflictsState) => (
            conflictId: number,
        ): object | null => state.items.find(conflict => conflict.id === conflictId) || null,

    },

    mutations: {

        [FINISH_FETCHING_CONFLICTS]: (
            state: IConflictsState,
            conflicts: any,
        ) => {
            state.items = conflicts
        },

        [SET_SELECTED_CONFLICT_ID]: (
            state: IConflictsState,
            selectedConflictId: number,
        ) => {
            state.selectedConflictId = selectedConflictId
        },

    },

    actions: {

        async [FETCH_CONFLICTS](
            { commit, state },
        ): Promise<void> {
            const preRequestConflicts = state.items

            commit(START_LOADING)

            try {
                const conflicts = await fetchConflicts()

                commit(FINISH_FETCHING_CONFLICTS, conflicts)
            }
            catch (error) {
                commit(FINISH_FETCHING_CONFLICTS, preRequestConflicts)

                throw error
            }
            finally {
                commit(FINISH_LOADING)
            }
        },

    },

}

export default conflictsModule
