







































import Vue from 'vue'
import moment from 'moment'

// Route names
import { EVENT_DETAILS_ROUTE_NAME } from '@/router/route-names'


export default Vue.extend({
    name: 'BaseEventCard',

    props: {
        // Данные о событии
        eventData: {
            type: Object,
            required: true,
        },
    },

    methods: {
        formatDate(date: string): string {
            return moment(date).format('D MMMM YYYY')
        },

        onActionButtonClick(): void {
            this.$router.push({
                name: EVENT_DETAILS_ROUTE_NAME,
                params: {
                    id: this.$props.eventData.id,
                },
            })
        },
    },
})
