
































import Vue from 'vue'
import { mapState } from 'vuex'

// Components
import BaseConflictInfo from '@/components/BaseConflictInfo'
import BaseEventInfo from '@/components/BaseEventInfo'

import { isNaturalNumber } from '@/utils'

// Route names
import { HOME_ROUTE_NAME } from '@/router/route-names'

// Store
import { GET_EVENT } from '@/store/modules/events/getter-types'
import { READ_EVENT } from '@/store/modules/events/action-types'


export default Vue.extend({
    name: 'ConflictDetails',

    components: {
        BaseConflictInfo,
        BaseEventInfo,
    },

    computed: {
        ...mapState({
            // @ts-ignore
            isLoaded: state => state.isLoaded,
            // @ts-ignore
            isLoading: state => state.isLoading,
        }),

        event(): object {
            return this.$store.getters[GET_EVENT](
                Number(this.$store.state.route.params.id),
            )
        },
    },

    beforeRouteEnter(to, from, next) {
        if (!isNaturalNumber(Number(to.params.id))) {
            next({
                name: HOME_ROUTE_NAME,
                replace: true,
            })
        }
        // Поскольку доступ к экземпляру компонента (this) ДО разрешения пути невозможен,
        // разрешаем переход при первом обращении к маршруту
        else {
            next(async (vm) => {
                try {
                    await vm.$store.dispatch(
                        READ_EVENT,
                        {
                            id: vm.$store.state.route.params.id,
                        },
                    )
                }

                // Если событие отсутствует, то мы не можем отобразить компонент,
                // поэтому переходим на главнуб страницу.
                // TODO Остальные ошибки нужно кидать дальше и обрабатывать глобальным обработчиком
                catch (error) {
                    vm.$router.replace({
                        name: HOME_ROUTE_NAME,
                    })
                }
            })
        }
    },

    async beforeRouteUpdate(to, from, next) {
        // Если `id` невалиден - "отменяем" переход
        if (!isNaturalNumber(Number(to.params.id))) {
            next(false)
        }
        else {
            // Иначе пробуем запросить у сервера сущность с указанным `id` и разрешаем переход,
            // если все закончилось хорошо
            try {
                await this.$store.dispatch(
                    READ_EVENT,
                    {
                        id: to.params.id,
                    },
                )
                next({
                    params: {
                        id: to.params.id,
                    },
                })
            }

            // Либо разрешаем переход и переходим на состояние до перехода.
            // `next(false)` не отменяет переход, а добавляет в историю переходов `from`,
            // то есть состояние до вызова хука
            catch (error) {
                // TODO Нужно ли как то дополнительно обрабатывать? Модалка, и.т.д..
                next(false)
            }
        }
    },

})
