






































import Vue from 'vue'
import { mapState } from 'vuex'

// Components
import BaseEventCard from '@/components/BaseEventCard'
import TheCreateEventForm from '@/components/TheCreateEventForm'

// Store actions
import { FETCH_EVENTS } from '@/store/modules/events/action-types'


export default Vue.extend({
    name: 'home',

    components: {
        BaseEventCard,
        TheCreateEventForm,
    },

    data: (): any => ({
        isCreateDialogShown: false,
    }),

    computed: {
        ...mapState({
            // @ts-ignore
            events: state => state.events.items,
            // @ts-ignore
            isLoading: state => state.isLoading,
        }),

        itemsInLastGridRowCount(): number {
            let count = this.events.length % 2

            // Если количество элементов нацело делится на 2,
            // значит последний ряд полностью заполнен
            if (count === 0) {
                count = 2
            }

            return count
        },
    },

    methods: {
        async handleCreateEvent(): Promise<void> {
            await this.$store.dispatch(FETCH_EVENTS)
        },
    },

    async created() {
        await this.$store.dispatch(FETCH_EVENTS)
    },
})
