import Vue from 'vue'
import { Module } from 'vuex'
import {
    indexOf,
    findIndex,
} from 'lodash'

// Definitions
import IRootState from '@/store/definitions/IRootState.d'
import IEventsState from './definitions/IEventsState.d'

import {
    FETCH_EVENTS,
    READ_EVENT,
} from './action-types'
import {
    FINISH_FETCHING_EVENTS,
    FINISH_READING_EVENT,
} from './mutation-types'
import {
    START_LOADING,
    FINISH_LOADING,
} from '@/store/mutation-types'
import { GET_EVENT } from './getter-types'

import {
    fetchEvents,
    fetchEvent,
} from '@/api'



const eventsModule: Module<IEventsState, IRootState> = {

    state: {
        items: [],
    },

    getters: {

        [GET_EVENT]: (state: IEventsState) => (
            eventId: number,
        ): object | null => state.items.find(event => event.id === eventId) || null,

    },

    mutations: {

        [FINISH_FETCHING_EVENTS]: (
            state: IEventsState,
            events: any,
        ) => {
            state.items = events
        },

        [FINISH_READING_EVENT](state, readItem) {
            if (readItem) {
                const appropriateItemIndex =
                    findIndex(state.items, item => item.id === readItem.id)

                // Если в наборе найден элемент, который соответствует полученному с сервера
                // Нужно наложить элемент из набора на зачитанный, потому что мы не должны
                // потерять внесенные изменения польвоателем
                if (appropriateItemIndex !== -1) {
                    Vue.set(
                        state.items,
                        indexOf(state.items, state.items[appropriateItemIndex]),
                        { ...readItem, ...state.items[appropriateItemIndex] },
                    )
                }
                // Если соответствующий элемент не найден в наборе,
                // просто добавим его в конец набора
                else {
                    state.items.push(readItem)
                }
            }
        },

    },

    actions: {

        async [FETCH_EVENTS](
            { commit, state },
        ): Promise<void> {
            const preRequestConflicts = state.items

            commit(START_LOADING)

            try {
                const events = await fetchEvents()

                commit(FINISH_FETCHING_EVENTS, events)
                commit(FINISH_LOADING)
            }
            catch (error) {
                commit(FINISH_FETCHING_EVENTS, preRequestConflicts)
                commit(FINISH_LOADING)

                throw error
            }
        },

        async [READ_EVENT](
            { commit, state },
            params: any,
        ): Promise<void> {

            commit(START_LOADING)

            try {
                const event = await fetchEvent({
                    ...params,
                })


                commit(FINISH_READING_EVENT, event)
                commit(FINISH_LOADING)

            }
            catch (error) {
                commit(FINISH_READING_EVENT, null)
                commit(FINISH_LOADING)

                throw error
            }

        },

    },

}

export default eventsModule
