


























































































































import Vue from 'vue'
import { mapState } from 'vuex'
// @ts-ignore
import InputTag from '@voerro/vue-tagsinput'

import { createEvent, fetchConflicts } from '@/api'

// Store actions
import { FETCH_CONFLICTS } from '@/store/modules/conflicts/action-types'


export default Vue.extend({
    name: 'TheCreateEventForm',

    components: {
        InputTag,
    },

    props: {
        // Определяет видимость компонента
        isVisible: {
            type: Boolean,
            required: true,
        },
    },

    data: (): any => ({
        eventFormData: {
            conflict_id: null,
            title: '',
            content: '',
            date: '',
            event_type_id: null,
            event_status_id: null,
            tags: [],
            image_urls: [],
            source_link: '',
        },

        formParameters: {
            isConflictsFetching: false,
            isFormLoading: false,
            isTagInputShown: false,
            tagInputValue: '',
        },

        conflicts: null,
    }),

    computed: {
        ...mapState({
            // @ts-ignore
            eventTypes: state => state.references.eventTypes,
            // @ts-ignore
            eventStatuses: state => state.references.eventStatuses,
        }),
    },

    methods: {
        async onSubmit(): Promise<void> {
            this.formParameters.isFormLoading = true

            try {
                await createEvent(this.eventFormData)

                this.$emit('update:dialogVisible', false)
                this.$emit('submit:success')
                this.formParameters.isFormLoading = false

                this.$message({
                    message: 'Новость успешно добавлена!',
                    type: 'success',
                })

                this.$destroy()
            }
            catch (error) {
                this.formParameters.isFormLoading = false

                throw error
            }
        },

        handleCloseDialog(done: Function): void {
            done()
        },

        async handleConflictSelectVisibilityChanging(isVisible: boolean): Promise<void> {
            if (isVisible) {
                try {
                    this.formParameters.isConflictsFetching = true
                    this.conflicts = await fetchConflicts({
                        brief: 1,
                    })
                }
                catch (error) {
                    throw error
                }
                finally {
                    this.formParameters.isConflictsFetching = false
                }
            }
        },
    },
})
