
































import Vue from 'vue'
// @ts-ignore
import VMarkerCluster from 'vue2-leaflet-markercluster'
import {
    LMap,
    LTileLayer,
    LMarker,
    LControlZoom,
} from 'vue2-leaflet'

// eslint-disable-next-line import/no-extraneous-dependencies
import L, { MapOptions, Icon } from 'leaflet'

import IMapMarker from './definitions/IMapMarker.d'


interface IComponentData {
    icon: Icon
    mapZoom: number
    mapMinZoom: number
    mapCenter: Array<number>
    mapOptions: MapOptions
    tileUrl: string
    zoomControlPosition: string
}

export default Vue.extend({
    name: 'TheMap',

    components: {
        LControlZoom,
        LMap,
        LMarker,
        LTileLayer,
        VMarkerCluster,
    },

    props: {
        // Массив маркеров для карты
        markers: {
            type: Array,
            required: true,
        },

        // Текущий выбранный конфликт
        selectedConflict: {
            type: Object || null,
            required: true,
        },
    },

    data: (): IComponentData => ({
        icon: L.icon({
            iconUrl: require('../assets/icons/map-marker.png'),
            iconSize: [24, 24],
            iconAnchor: [12, 24],
        }),
        mapZoom: 5,
        mapMinZoom: 3,
        mapCenter: [55.753739, 37.619895],
        mapOptions: {
            attributionControl: false,
            zoomControl: false,
        },
        zoomControlPosition: 'topright',
        tileUrl: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
    }),

    created() {
        if (this.$props.selectedConflict) {
            this.mapCenter =
                [this.$props.selectedConflict.latitude, this.$props.selectedConflict.longitude]

            this.mapZoom = 13
        }
    },

    methods: {
        transformMarkerCoordinates(marker: IMapMarker): Array<number> {
            return [marker.latitude, marker.longitude]
        },

        onMarkerClick(marker: IMapMarker): void {
            this.mapCenter = [marker.latitude, marker.longitude]

            this.$emit('selected-conflict-changed', marker.id)
        },
    },
})
