



















































































import Vue from 'vue'
import { mapState } from 'vuex'
import moment from 'moment'

// Store
import { SET_SELECTED_CONFLICT_ID } from '@/store/modules/conflicts/mutation-types'

// Route names
import { MAP_ROUTE_NAME } from '@/router/route-names'


export default Vue.extend({
    name: 'BaseConflictInfo',

    props: {
        // Связанный конфликт
        conflictData: {
            type: Object || null,
            required: true,
        },
    },

    computed: {
        ...mapState({
            // @ts-ignore
            conflictReasons: state => state.references.conflictReasons,
            // @ts-ignore
            conflictResults: state => state.references.conflictResults,
            // @ts-ignore
            regions: state => state.references.regions,
            // @ts-ignore
            industries: state => state.references.industries,
        }),

        industryName(): string {
            // @ts-ignore
            return this.industries.find(
                (industry: any) => industry.id === this.$props.conflictData.industry_id,
            ).name
        },

        formattedDate(): string {
            return moment(this.$props.conflictData.created_at).format('D MMMM YYYY')
        },

        reasonName(): string {
            // @ts-ignore
            return this.conflictReasons.find(
                (reason: any) => reason.id === this.$props.conflictData.conflict_reason_id,
            ).name
        },

        resultName(): string {
            // @ts-ignore
            return this.conflictResults.find(
                (result: any) => result.id === this.$props.conflictData.conflict_result_id,
            ).name
        },

        regionName(): string {
            // @ts-ignore
            return this.regions.find(
                (region: any) => region.id === this.$props.conflictData.region_id,
            ).name
        },
    },

    methods: {
        onActionButtonClick(): void {
            this.$store.commit(SET_SELECTED_CONFLICT_ID, this.$props.conflictData.id)

            this.$router.push({
                name: MAP_ROUTE_NAME,
            })
        },
    },
})
